import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import App from './App';
import { AuthProvider } from './contexts/AuthContext';
import { PWAProvider } from './contexts/PWAContext';
import './styles/colors.css';
import './styles/responsive.css';
import './styles/navbar-fix.css';

// Default color values (fallback)
const defaultPrimaryMain = '#000776';
const defaultSecondaryMain = '#8E8982';
const defaultBackgroundDefault = '#F8F9FA';
const defaultTextPrimary = '#0A1128';
const defaultTextSecondary = '#36454F';

// Use environment variables or default values
const primaryMain = process.env.REACT_APP_PRIMARY_MAIN || defaultPrimaryMain;
const secondaryMain = process.env.REACT_APP_SECONDARY_MAIN || defaultSecondaryMain;
const backgroundDefault = process.env.REACT_APP_BACKGROUND_DEFAULT || defaultBackgroundDefault;
const textPrimary = process.env.REACT_APP_TEXT_PRIMARY || defaultTextPrimary;
const textSecondary = process.env.REACT_APP_TEXT_SECONDARY || defaultTextSecondary;

// Create Material-UI theme - Navy Blue & Gray Design System
const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: primaryMain,      // Deep Navy
      light: '#003366',     // Medium Navy  
      dark: '#0A1128',      // Dark Navy
      contrastText: '#FFFFFF',
    },
    secondary: {
      main: secondaryMain,      // Medium Gray
      light: '#C0C0C0',     // Cool Gray
      dark: '#36454F',      // Charcoal Gray
      contrastText: '#FFFFFF',
    },
    background: {
      default: backgroundDefault,   // Light Gray
      paper: '#FFFFFF',     // Pure White
    },
    text: {
      primary: textPrimary,   // Dark Navy Text
      secondary: textSecondary, // Charcoal Gray Text
    },
    grey: {
      50: '#F8F9FA',
      100: '#f1f5f9',
      200: '#e2e8f0',
      300: '#C0C0C0',
      400: '#94a3b8',
      500: '#8E8982',
      600: '#36454F',
      700: '#334155',
      800: '#0A1128',
      900: '#000000',
    },
    success: {
      main: '#059669',      // Emerald Green
      light: '#10b981',
      dark: '#047857',
    },
    warning: {
      main: '#d97706',      // Amber
      light: '#f59e0b',
      dark: '#b45309',
    },
    error: {
      main: '#dc2626',      // Red
      light: '#ef4444',
      dark: '#b91c1c',
    },
    info: {
      main: '#003366',      // Secondary navy for info
      light: '#000776',
      dark: '#0A1128',
    },
  },
  typography: {
    fontFamily: '"Inter", -apple-system, BlinkMacSystemFont, sans-serif',
    h1: {
      fontFamily: '"Poppins", sans-serif',
      fontSize: '2.25rem',
      fontWeight: 600,
      color: '#0A1128',
    },
    h2: {
      fontFamily: '"Poppins", sans-serif',
      fontSize: '1.875rem',
      fontWeight: 600,
      color: '#0A1128',
    },
    h3: {
      fontFamily: '"Poppins", sans-serif',
      fontSize: '1.5rem',
      fontWeight: 600,
      color: '#0A1128',
    },
    h4: {
      fontFamily: '"Poppins", sans-serif',
      fontSize: '1.25rem',
      fontWeight: 500,
      color: '#0A1128',
    },
    h5: {
      fontFamily: '"Poppins", sans-serif',
      fontSize: '1.125rem',
      fontWeight: 500,
      color: '#0A1128',
    },
    h6: {
      fontFamily: '"Poppins", sans-serif',
      fontSize: '1rem',
      fontWeight: 500,
      color: '#0A1128',
    },
    body1: {
      fontSize: '1rem',
      color: '#36454F',
    },
    body2: {
      fontSize: '0.875rem',
      color: '#8E8982',
    },
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          borderRadius: 8,
          fontFamily: 'var(--font-family-primary)',
          fontWeight: 500,
          transition: 'var(--transition-normal)',
          '&:focus': {
            outline: '2px solid var(--color-secondary-navy)',
            outlineOffset: '2px',
          },
        },
        contained: {
          backgroundColor: 'var(--color-primary-navy)',
          color: 'var(--color-white)',
          '&:hover': {
            backgroundColor: 'var(--color-secondary-navy)',
          },
        },
        outlined: {
          borderColor: 'var(--color-secondary-navy)',
          color: 'var(--color-secondary-navy)',
          '&:hover': {
            borderColor: 'var(--color-primary-navy)',
            backgroundColor: 'rgba(0, 7, 118, 0.04)',
          },
        },
        text: {
          color: 'var(--color-primary-navy)',
          '&:hover': {
            backgroundColor: 'rgba(0, 7, 118, 0.04)',
          },
        },
      },
    },
    MuiIconButton: {
      styleOverrides: {
        root: {
          transition: 'var(--transition-normal)',
          '&:focus': {
            outline: '2px solid var(--color-secondary-navy)',
            outlineOffset: '2px',
          },
          '&:hover': {
            backgroundColor: 'rgba(0, 7, 118, 0.04)',
          },
        },
      },
    },
    MuiFab: {
      styleOverrides: {
        root: {
          backgroundColor: 'var(--color-primary-navy)',
          color: 'var(--color-white)',
          transition: 'var(--transition-normal)',
          '&:hover': {
            backgroundColor: 'var(--color-secondary-navy)',
          },
          '&:focus': {
            outline: '2px solid var(--color-secondary-navy)',
            outlineOffset: '2px',
          },
        },
      },
    },
    MuiChip: {
      styleOverrides: {
        root: {
          fontFamily: 'var(--font-family-primary)',
          transition: 'var(--transition-normal)',
          '&:focus': {
            outline: '2px solid var(--color-secondary-navy)',
            outlineOffset: '2px',
          },
        },
        filled: {
          backgroundColor: 'var(--color-light-gray)',
          color: 'var(--color-tertiary-navy)',
          '&:hover': {
            backgroundColor: 'var(--color-cool-gray)',
          },
        },
        outlined: {
          borderColor: 'var(--color-cool-gray)',
          color: 'var(--color-tertiary-navy)',
          '&:hover': {
            backgroundColor: 'var(--color-light-gray)',
          },
        },
      },
    },
    MuiCard: {
      styleOverrides: {
        root: {
          borderRadius: 12,
          boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
          backgroundColor: 'var(--color-white)',
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            borderRadius: 8,
            fontFamily: 'var(--font-family-primary)',
            transition: 'var(--transition-normal)',
            '& fieldset': {
              borderColor: 'var(--color-cool-gray)',
            },
            '&:hover fieldset': {
              borderColor: 'var(--color-medium-gray)',
            },
            '&.Mui-focused fieldset': {
              borderColor: 'var(--color-primary-navy)',
              borderWidth: '2px',
            },
            '&.Mui-error fieldset': {
              borderColor: '#dc2626',
            },
          },
          '& .MuiInputLabel-root': {
            color: 'var(--color-charcoal-gray)',
            fontFamily: 'var(--font-family-primary)',
            '&.Mui-focused': {
              color: 'var(--color-primary-navy)',
            },
            '&.Mui-error': {
              color: '#dc2626',
            },
          },
          '& .MuiInputBase-input': {
            color: 'var(--color-tertiary-navy)',
            fontFamily: 'var(--font-family-primary)',
          },
          '& .MuiFormHelperText-root': {
            fontFamily: 'var(--font-family-primary)',
            '&.Mui-error': {
              color: '#dc2626',
            },
          },
        },
      },
    },
    MuiSelect: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-notchedOutline': {
            borderColor: 'var(--color-cool-gray)',
          },
          '&:hover .MuiOutlinedInput-notchedOutline': {
            borderColor: 'var(--color-medium-gray)',
          },
          '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
            borderColor: 'var(--color-primary-navy)',
            borderWidth: '2px',
          },
        },
      },
    },
    MuiFormControl: {
      styleOverrides: {
        root: {
          '& .MuiFormLabel-root': {
            color: 'var(--color-charcoal-gray)',
            fontFamily: 'var(--font-family-primary)',
            '&.Mui-focused': {
              color: 'var(--color-primary-navy)',
            },
          },
        },
      },
    },
    MuiCheckbox: {
      styleOverrides: {
        root: {
          color: 'var(--color-cool-gray)',
          transition: 'var(--transition-normal)',
          '&.Mui-checked': {
            color: 'var(--color-primary-navy)',
          },
          '&:focus': {
            outline: '2px solid var(--color-secondary-navy)',
            outlineOffset: '2px',
          },
        },
      },
    },
    MuiRadio: {
      styleOverrides: {
        root: {
          color: 'var(--color-cool-gray)',
          transition: 'var(--transition-normal)',
          '&.Mui-checked': {
            color: 'var(--color-primary-navy)',
          },
          '&:focus': {
            outline: '2px solid var(--color-secondary-navy)',
            outlineOffset: '2px',
          },
        },
      },
    },
    MuiSwitch: {
      styleOverrides: {
        root: {
          '& .MuiSwitch-switchBase': {
            '&.Mui-checked': {
              color: 'var(--color-primary-navy)',
              '& + .MuiSwitch-track': {
                backgroundColor: 'var(--color-primary-navy)',
              },
            },
            '&:focus': {
              outline: '2px solid var(--color-secondary-navy)',
              outlineOffset: '2px',
            },
          },
          '& .MuiSwitch-track': {
            backgroundColor: 'var(--color-cool-gray)',
          },
        },
      },
    },
    MuiAppBar: {
      styleOverrides: {
        root: {
          backgroundColor: 'var(--color-primary-navy)',
          color: 'var(--color-white)',
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          backgroundColor: 'var(--color-white)',
          borderRadius: 12,
        },
      },
    },
    MuiContainer: {
      styleOverrides: {
        root: {
          backgroundColor: 'transparent',
        },
      },
    },
    MuiDialog: {
      styleOverrides: {
        paper: {
          backgroundColor: 'var(--color-white)',
          borderRadius: 12,
        },
      },
    },
    MuiMenu: {
      styleOverrides: {
        paper: {
          backgroundColor: 'var(--color-white)',
          borderRadius: 8,
          boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
        },
      },
    },
    MuiDrawer: {
      styleOverrides: {
        paper: {
          backgroundColor: 'var(--color-white)',
        },
      },
    },
  },
});

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <AuthProvider>
          <PWAProvider>
            <App />
          </PWAProvider>
        </AuthProvider>
      </ThemeProvider>
    </BrowserRouter>
  </React.StrictMode>
);
